}

h3 {
  font-size: 24.5px;
  color: #343434;
}

h3 span {
  color: #d0d0d0;
  font-weight: normal;
  font-size: 24.5px;
}

.show-more {
  padding-top: 30px;
}

.show-more a {
  color: #506aa1;
  font-size: 16px;
  text-decoration: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.13);
}

.show-more a:hover {
  opacity: .4;
  transition: all .3s;
}

.sidebar-menu {
  position: fixed;
  left: 37px;
  z-index: 999;
}

.sidebar-menu .notification-bg {
  position: absolute;
  left: 11px;
  bottom: -20px;
}

.sidebar-menu .notification-count {
  color: #ffffff;
  position: absolute;
  bottom: 2px;
  right: 3px;
}

.sidebar-menu #show-sidebar {
  display: none;
}

.sidebar-menu input[type=checkbox]:checked ~ .sidebar {
  opacity: 1;
  visibility: visible;
  transition: all .4s;
}

.sidebar-menu input[type=checkbox]:checked ~ label {
  transform: rotate(45deg);
  transition: all .4s;
}

.sidebar-menu label {
  display: none;
}

@media screen and (max-width: 1380px) {
  .sidebar-menu label {
    display: block;
    position: fixed;
    bottom: 10px;
  }
}

.sidebar-menu .sidebar {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 1380px) {
  .sidebar-menu .sidebar {
    visibility: hidden;
    opacity: 0;
  }
}

.sidebar-menu .sidebar li {
  position: relative;
  padding-top: 15px;
}

.sidebar-menu .sidebar li a {
  text-decoration: none;
}

.bg-ellipse {
  background: url("../images/ellipse.png") no-repeat 100% 0;
}

#header {
